///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Imports //////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////
@import 'variables';
@import '../libs/bootstrap-4.0.0-beta.2/scss/bootstrap';
@import '../libs/fullcalendar-3.6.1/fullcalendar';
///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Wordpress admin bar //////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////

body.logged-in.admin-bar {
	#wpadminbar {
		position : absolute !important;
		#wp-admin-bar-my-account > .ab-item:before {
			float        : left;
			margin-right : 6px;
			margin-left  : 0;
		}
		@media print {
			display : none !important;
		}
	}
}

///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Semantic styling /////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////
html {
	font-size : $base-font-size;
}

body {
	font-family      : $body-font;
	font-weight      : $body-weight;
	color            : $body-text;
	background-color : $body-background;
}

h1, h2, h3, h4, h5, h6 {
	font-family            : $header-font;
	font-weight            : $header-weight;
	color                  : $header-text;
	line-height            : normal;
	-webkit-font-smoothing : antialiased
}

h1 {
	font-size : $h1-size;
}

h2 {
	font-size : $h2-size;
}

h3 {
	font-size : $h3-size;
}

h4 {
	font-size : $h4-size;
}

h5 {
	font-size : $h5-size;
}

h6 {
	font-size : $h6-size;
}

a {
	color : $link-text;
	&:hover {
		color : $link-text;
	}
}

img {
	max-width : none;
	&.alignleft {
		float         : left;
		margin-right  : 25px;
		margin-bottom : 10px;
	}
	&.alignright {
		float         : right;
		margin-left   : 25px;
		margin-bottom : 10px;
	}
	&.img-thumbnail {
		border-radius    : $border-radius;
		background-color : transparent;
	}
}

///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Bootstrap overrides //////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////
.btn {
	-webkit-border-radius : $border-radius;
	-moz-border-radius    : $border-radius;
	border-radius         : $border-radius;
	text-transform        : uppercase;
}

table {
	@extend .table;
	width         : 100%;
	max-width     : 100%;
	margin-bottom : 0;
	border        : 1px solid #ddd;
	th {
		background-color : $table-th-background;
		color            : $table-th-text;
		font-family      : $header-font;
		font-size        : 0.875rem;
		font-weight      : normal;
		text-transform   : uppercase;
		//border-bottom : solid 1px #ddd !important;
	}
	td:not(:last-child), th:not(:last-child) {
		border-right : 1px solid #ddd;
	}
	th, td {
		border-color : #ddd !important;
		border-width : 1px !important;
	}
}

.gallery {
	display   : flex;
	flex-wrap : wrap;
	//justify-content : space-around;
	padding   : 0.5rem 0;
	.gallery-item {
		position : relative;
		padding  : 1px;
		.img-fluid {
			width : 100%;
		}
		.gallery-caption {
			display          : block;
			position         : absolute;
			bottom           : 0px;
			left             : 0px;
			right            : 0px;
			padding          : 0.25rem 0.5rem;
			margin           : 0 1px 1px;
			background-color : rgba(0, 0, 0, 0.75);
			color            : #fff;
			font-size        : 0.875rem;
			text-shadow      : 1px 1px 1px #000;
		}
		&:hover > .gallery-caption {
			display : none;
		}
	}
	@include media-breakpoint-up(xl) {
		&.columns-1 > .column {
			width : 100%;
		}
		&.columns-2 > .column {
			width : 50%;
		}
		&.columns-3 > .column {
			width : 33.33%;
		}
		&.columns-4 > .column {
			width : 25%;
		}
		&.columns-5 > .column {
			width : 20%;
		}
		&.columns-6 > .column {
			width : 16.66%;
		}
		&.columns-7 > .column {
			width : 14.28%;
		}
		&.columns-8 > .column {
			width : 12.5%;
		}
		&.columns-9 > .column {
			width : 11.11%;
		}
	}
}

