///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Comments /////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////
.comments-wrapper {
	background-color : #eee;
	padding          : 1rem 0;
	@media print {
		display : none !important;
	}
}

.comments-list {
	list-style   : none;
	padding-left : 0;
	> li:not(:last-of-type) {
		padding-bottom : 1rem;
		border-bottom  : solid 2px #999;
	}
	> li:not(:first-of-type) {
		padding-top : 0.75rem;
	}
	article.comment-body {
		font-size     : 0.875rem;
		margin-bottom : 0;
		div.comment-content > p:last-of-type {
			margin-bottom : 0;
		}
	}
	.fn:after {
		content : ' commented:';
	}
	ul.children {
		list-style   : none;
		padding-left : 0;
		padding-top  : 1rem;
		.fn:after {
			content : ' replied:';
		}
	}
	footer.comment-meta {
		font-size     : 0.8125rem;
		color         : #666;
		margin-bottom : 0.25rem;
		b {
			font-weight : normal !important;
		}
		.fn:after {
			font-size : 0.8125rem;
		}
	}
	a {
		display : none;
	}

	.says {
		display : none;
	}

	.comment-metadata {
		display : none;
	}
	.avatar.avatar-32 {
		height         : 20px;
		width          : 20px;
		vertical-align : top;
	}
}

///////////////////////////////////////////////////////////////////////////////////////////////////////
//////////////////////////////////////// Widget ///////////////////////////////////////////////////////
///////////////////////////////////////////////////////////////////////////////////////////////////////
.widget {
	h3 {
		font-size : 1rem;
	}
	.list-group {
		.list-group-item {
			display          : flex;
			margin           : 0;
			padding          : 0.5rem 1rem;
			border           : none;
			border-radius    : 0;
			background-color : transparent;
		}
		.list-group-item-feature {
			margin-right : 1rem;
		}
	}
	.calendar-icon {
		max-width   : 50px;
		width       : 50px;
		max-height  : 50px;
		height      : 50px;
		font-family : $calendar-font;
		text-align  : center;
		.month {
			height                  : 1.125rem;
			font-size               : 0.625rem;
			line-height             : 1.125rem;
			background-color        : #333;
			border                  : solid 1px #333;
			border-bottom           : none;
			border-top-left-radius  : $border-radius;
			border-top-right-radius : $border-radius;
			color                   : #fff;
		}
		.day {
			height                     : 2rem;
			font-size                  : 1.125rem;
			line-height                : 2rem;
			background-color           : #fff;
			border                     : solid 1px #333;
			border-top                 : none;
			border-bottom-left-radius  : $border-radius;
			border-bottom-right-radius : $border-radius;
			color                      : $body-text !important;
		}
	}
	.more-link {
		font-size      : 0.8125rem;
		font-weight    : 400;
		text-transform : uppercase;
		&:hover {
			text-decoration  : underline !important;
			background-color : transparent !important;
		}
	}
}

