		}
		h3, h4, h5, h6 {
			color : $side-widget-body-text !important;
		}
		.list-group {
			padding                    : $side-widget-body-padding;
			margin                     : $side-widget-body-margin;
			color                      : $side-widget-body-text;
			background-color           : $side-widget-body-background;
			border                     : solid 1px $side-widget-body-border;
			border-top-left-radius     : $side-widget-body-border-top-left-radius;
			border-top-right-radius    : $side-widget-body-border-top-right-radius;
			border-bottom-left-radius  : $side-widget-body-border-bottom-left-radius;
			border-bottom-right-radius : $side-widget-body-border-bottom-right-radius;
			.list-group-item {
				padding : $side-widget-body-item-padding;
				margin  : $side-widget-body-item-margin;
			}
			a {
				color : $side-widget-body-link-text !important;
				&:hover,
				&.active,
				&.active:hover {
					background-color : rgba(0, 0, 0, 0.1);
					text-decoration  : none;
				}
			}
		}
		> table {
			margin : $side-widget-body-padding;
		}
	}
	@media print {
		display : none !important;
	}
	@include media-breakpoint-down(lg) {
		.widget:last-of-type {
			padding-bottom : 2rem;
		}
	}
	@include media-breakpoint-up(xl) {
		.widget:first-of-type {
			padding-top : 4rem;
		}
		.widget:last-of-type {
			padding-bottom : 4rem;
		}
	}
}

