			border                     : solid 1px $main-widget-body-border;
			border-top-left-radius     : $main-widget-body-border-top-left-radius;
			border-top-right-radius    : $main-widget-body-border-top-right-radius;
			border-bottom-left-radius  : $main-widget-body-border-bottom-left-radius;
			border-bottom-right-radius : $main-widget-body-border-bottom-right-radius;
			.list-group-item {
				padding : $main-widget-body-item-padding;
				margin  : $main-widget-body-item-margin;
			}
			a {
				color : $main-widget-body-link-text !important;
				&:hover,
				&.active,
				&.active:hover {
					background-color : transparent;
					text-decoration  : underline;
				}
			}
		}
		> table {
			margin : $main-widget-body-padding;
		}
	}
}

// Header + footer sidebars
body > section.sidebar {
	&.bleed {
		.widget {
			padding : 0 !important;
			margin  : 0 !important;
			border  : none !important;
			h2 {
				display : none;
			}
			> div {
				padding : 0;
				margin  : 0;
			}
			.embed-responsive {
				height : 100%
			}
			.gallery-group .gallery-group-item {
				padding : 0;
				margin  : 0;
			}
			&:nth-of-type(even) {
				background-color : rgba(0, 0, 0, 0.1);
			}
		}
	}
	&:not(.bleed) {
		@include media-breakpoint-down(lg) {
			.widget {
				padding-top    : 2rem;
				padding-bottom : 2rem;
				h2 {
					padding : 0;
					margin  : 0 0 0.5rem;
				}
				&:first-child {
					padding-top : 4rem;
				}
				&:last-child {
					padding-bottom : 4rem;
				}
				&.syn-google-map-widget {
					min-height : 400px;
				}
			}
		}
		@include media-breakpoint-up(xl) {
			.widget {
				padding : $header-footer-widget-padding;
				margin  : $header-footer-widget-margin;
				h2 {
					padding        : $header-footer-widget-title-padding;
					margin         : $header-footer-widget-title-margin;
					font-size      : 1.25rem;
					text-transform : uppercase;
				}
				.list-group {
					padding-top                : $header-footer-widget-body-padding-top;
					padding-bottom             : $header-footer-widget-body-padding-bottom;
					margin                     : $header-footer-widget-body-margin;
					border-top-left-radius     : $header-footer-widget-body-border-top-left-radius;
					border-top-right-radius    : $header-footer-widget-body-border-top-right-radius;
					border-bottom-left-radius  : $header-footer-widget-body-border-bottom-left-radius;
					border-bottom-right-radius : $header-footer-widget-body-border-bottom-right-radius;
				}
				> table {
					margin-top    : $header-footer-widget-body-padding-top;
					margin-bottom : $header-footer-widget-body-padding-bottom;
					margin-left   : 0;
					margin-right  : 0;
					thead {
						background-color : rgba(0, 0, 0, 0.1);
					}
				}
				a {
					&:hover,
					&.active,
					&.active:hover {
						background-color : rgba(0, 0, 0, 0.1);
						text-decoration  : none;
					}
				}
				.list-group-item {
					padding-top    : $header-footer-widget-body-item-padding-top;
					padding-right  : $header-footer-widget-body-item-padding-right;
					padding-bottom : $header-footer-widget-body-item-padding-bottom;
					padding-left   : $header-footer-widget-body-item-padding-left;
					margin         : $header-footer-widget-body-item-margin;
				}
				&.syn-google-map-widget {
					min-height : 600px;
				}
			}
		}
	}
	@media print {
		display : none !important;
	}
}

