//////////////////////////////////////////////////////// reset Bootstrap colors
//$primary : $primary; defined in site scss
//$secondary : $secondary; defined in site scss
$success : #255d00;
$danger : #9a0007;
//$warning : #ffc107; not overridden
$info : #007c91;
$light : #f8f9fa;
$dark : #343a40;

//////////////////////////////////////////////////////// typography
$base-font-size : 16px;
$ubantu : Ubantu, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
$roboto : Roboto, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
$ubuntu-mono : 'Ubuntu Mono', -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, sans-serif;
// font assignments
$brand-font : $roboto;
$brand-weight : normal;
$navbar-font : $ubantu;
$navbar-weight : normal;
$header-font : $roboto;
$header-weight : normal;
$body-font : $ubantu;
$body-weight : normal;
$jumbotron-headline-font : $roboto;
$jumbotron-headline-weight : normal;
$form-font : $ubantu;
$form-weight : normal;
$widget-font : $ubantu;
$widget-weight : normal;
$calendar-font : $ubuntu-mono;
$calendar-weight : normal;
// header font sizes
$h1-size : 2rem;
$h2-size : 1.5rem;
$h3-size : 1.25rem;
$h4-size : 1.125rem;
$h5-size : 1rem;
$h6-size : 0.875rem;

//////////////////////////////////////////////////////// global colors
$body-background : #fff;
$body-text : $dark;
$header-text : $dark;
$link-text : $primary;
//////////////////////////////////////////////////////// border radius
$border-radius : 0;
//////////////////////////////////////////////////////// table
$table-th-background : $primary;
$table-th-text : #fff;
//////////////////////////////////////////////////////// navbar
$navbar-background : $primary;
$navbar-text : $primary-text;
$navbar-link-background-hover : $primary-light;
$navbar-height-lg : 5.125rem; // 5.125rem = 82px @ 16px/rem = 50px high brand with top/bottom padding of 1rem
$navbar-brand-margin-lg : 1rem; // top and bottom margin around brand
$navbar-height-md : 3.5rem; // 3.5rem = 56px @ 16px/rem = 24px high brand with top/bottom padding of 1rem
$navbar-brand-margin-md : 0.75rem; // top and bottom margin around brand
// navbar-brand
$brand-height-md : $navbar-height-md - ( 2 * $navbar-brand-margin-md );
$brand-height-lg : $navbar-height-lg - ( 2 * $navbar-brand-margin-lg );
//////////////////////////////////////////////////////// banner
$banner-background : $primary;
$banner-text : $primary-text;
//////////////////////////////////////////////////////// jumbotron
$jumbotron-background : $secondary;
$jumbotron-border : $secondary;
$jumbotron-text : $secondary-text;
$jumbotron-text-shadow : none; //$jumbotron-text-shadow: 0 3px 5px rgba(0, 0, 0, 0.24);
$jumbotron-button-background : $primary;
$jumbotron-button-border : $primary;
$jumbotron-button-text : $primary-text;
//////////////////////////////////////////////////////// breadcrumb
$breadcrumb-background : $secondary;
$breadcrumb-text : $secondary-text;
//////////////////////////////////////////////////////// page title badges
$page-title-badge-background : $secondary;
$page-title-badge-border : $secondary;
$page-title-badge-text : $secondary-text;
$page-title-badge-border-radius : 10rem;
//////////////////////////////////////////////////////// widget type
//$widget-type: solid;
//////////////////////////////////////////////////////// main left + right sidebar widgets
$side-widget-background : none;
$side-widget-border : none;
////////////////////////////////////////// solid widget
// widget title
$side-widget-title-padding : 0.5rem;
$side-widget-title-margin : 0; // avoid
$side-widget-title-background : $primary;
$side-widget-title-border : $primary;
$side-widget-title-border-top-left-radius : $border-radius;
$side-widget-title-border-top-right-radius : $border-radius;
$side-widget-title-border-bottom-left-radius : 0;
$side-widget-title-border-bottom-right-radius : 0;
$side-widget-title-text : $primary-text;
// widget body
$side-widget-body-padding : 0.5rem 0; // careful with this one! hard to rebalance
$side-widget-body-margin : 0; // avoid
$side-widget-body-background : $primary-light;
$side-widget-body-border : $primary-light;
$side-widget-body-border-top-left-radius : 0;
$side-widget-body-border-top-right-radius : 0;
$side-widget-body-border-bottom-left-radius : $border-radius;
$side-widget-body-border-bottom-right-radius : $border-radius;
$side-widget-body-text : $primary-text;
// widget items
$side-widget-body-item-padding : 0.5rem 1rem;
$side-widget-body-item-margin : 0; // avoid
// widget links
$side-widget-body-link-text : $primary-text;
//$side-widget-body-link-background-hover : $primary;
//$side-widget-body-link-text-hover : $primary-text;
